import { _ } from '@splunk/ui-utils/i18n';
import { COLOR_OR_TOKEN_PATTERN, getPattern } from '@splunk/visualizations-shared/schemaUtils';
import { SingleValueRadial as SingleValueRadialIcon } from '@splunk/visualization-icons';
import { VizConfig } from '../common/interfaces/VizConfig';
import { DataContract } from '../common/interfaces/DataContract';
import { VizSize } from '../common/interfaces/VizSize';
import NumberFormattingConfig from '../common/editorConfig/NumberFormatting';
import { DefaultContext } from '../common/interfaces/DefaultContext';
import { OptionsSchema } from '../common/interfaces/OptionsSchema';
import { EditorConfig } from '../common/interfaces/Editor';
import { VizBehavior } from '../common/interfaces/VizBehavior';
import { enhanceConfig } from '../common/utils/configUtils';
import { VizCategory } from '../common/interfaces/VizCategory';

const dataContract: DataContract = {
    requiredDataSources: [
        {
            name: 'primary',
            description: 'DataSource that powers the visualization',
        },
    ],
    optionalDataSources: [],
    initialRequestParams: {
        primary: { offset: 0, count: 10000 },
    },
};

const size: VizSize = {
    initialWidth: 250,
    initialHeight: 250,
};

const defaultContext: DefaultContext = {
    trendColors: [
        {
            from: 0,
            value: '#53A051',
        },
        {
            to: 0,
            value: '#DC4E41',
        },
    ],
};

const optionsSchema: OptionsSchema = {
    majorValue: {
        default: '> primary|seriesByIndex(0)|lastPoint()',
        description: 'The major value to display in the visualization.',
        type: 'number',
    },
    majorValueField: {
        default: '> majorValue | getField()',
        description: 'The field name of major value.',
        type: 'string',
    },
    majorColor: {
        description:
            'Specify the dataSource color for the major value. The hex value format should be #FFFFFF.',
        pattern: COLOR_OR_TOKEN_PATTERN,
        type: 'string',
    },
    trendValue: {
        default: '> primary|seriesByIndex(0)|delta(-2)', // todo: may need update delta method
        description: 'The trend value to display in the visualization.',
        type: 'number',
    },
    trendColor: {
        default: '> trendValue|rangeValue(trendColors)', // hmm what is the default color for trend...can use match...
        description: 'Specify the color for trend value. The hex value format should be #FFFFFF.',
        pattern: COLOR_OR_TOKEN_PATTERN,
        type: 'string',
    },
    trendDisplay: {
        default: 'absolute',
        description: 'Specify how to display the trend value.',
        pattern: getPattern(['percent', 'absolute', 'off']),
        type: 'string',
    },
    shouldAbbreviateTrendValue: {
        default: false,
        description:
            'Specify whether to abbreviate the trend value to 2 decimal points. A magnitude unit will be displayed.',
        type: 'boolean',
    },
    trendIndicatorPosition: {
        default: 'standard',
        description:
            'Specify where the trend indicator is displayed on a radial graph. Note: When the major value or trend value is too large to fit inside of the graph, the trend is displayed below the graph.',
        pattern: getPattern(['standard', 'below', 'outside']),
        type: 'string',
    },
    backgroundColor: {
        description:
            'Specify the color for the background. You may use a dataSource to apply the color. The default for enterprise light is "#ffffff". The default for enterprise dark is "#000000". The default for prisma dark is "#0b0c0e".',
        pattern: COLOR_OR_TOKEN_PATTERN,
        type: 'string',
    },
    unit: {
        description: 'Specify text to show next to the major value.',
        type: 'string',
    },
    unitPosition: {
        default: 'after',
        description: 'Specify whether the unit text should appear before or after the major value.',
        pattern: getPattern(['before', 'after']),
        type: 'string',
    },
    underLabel: { description: 'Specify the text that appears below the major value.', type: 'string' },
    numberPrecision: {
        default: 0,
        maximum: 20,
        minimum: 0,
        description:
            'Specify the number of decimal places to display. For example, to display 3 decimal places, use a value of 3. The maximum value is 20.',
        type: 'number',
    },
    shouldUseThousandSeparators: {
        default: true,
        description: 'Specify whether numeric values use commas as thousandths separaters.',
        type: 'boolean',
    },
    radialBackgroundColor: {
        description:
            'Specify the color for the radial background color. The hex value format should be #FFFFFF.',
        pattern: COLOR_OR_TOKEN_PATTERN,
        type: 'string',
    },
    radialStrokeColor: {
        description: 'Specify the color for the radial stroke color. The hex value format should be #FFFFFF.',
        pattern: COLOR_OR_TOKEN_PATTERN,
        type: 'string',
    },
};

const editorConfig: EditorConfig[] = [
    {
        label: _('Visualization Settings'),
        layout: [
            [
                {
                    label: _('Trend Display as'),
                    option: 'trendDisplay',
                    editor: 'editor.select',
                    editorProps: {
                        values: [
                            { label: _('Percent'), value: 'percent' },
                            { label: _('Absolute'), value: 'absolute' },
                            { label: _('Off'), value: 'off' },
                        ],
                    },
                },
            ],
            [
                {
                    label: _('Trend Indicator Position'),
                    option: 'trendIndicatorPosition',
                    editor: 'editor.radioBar',
                    editorProps: {
                        values: [
                            { label: _('Standard'), value: 'standard' },
                            { label: _('Below'), value: 'below' },
                            { label: _('Outside'), value: 'outside' },
                        ],
                    },
                    // If property matches the default value specified in schema, its not set in options. Hence we check if its undefined.
                    showEditor: ({ options }) => options.trendDisplay !== 'off',
                },
            ],
        ],
    },
    {
        label: _('Background Color'),
        layout: [
            [
                {
                    label: '',
                    option: 'backgroundColor',
                    editor: 'editor.color',
                    editorProps: {
                        themeVariable:
                            '@splunk/dashboard-visualizations/SingleValueRadial.defaultBackgroundColor',
                    },
                },
            ],
        ],
    },
    NumberFormattingConfig,
];
/**
 * visualization configuration
 */
const config: VizConfig = {
    /**
     * unique viz key
     */
    key: 'splunk.singlevalueradial',
    /**
     * viz name
     */
    name: 'Single Value Radial',
    category: VizCategory.SINGLE_VALUE,
    /**
     * viz icon
     */
    icon: SingleValueRadialIcon,
    dataContract,
    size,
    defaultContext,
    optionsSchema,
    editorConfig,
    events: {
        'value.click': {
            description: 'trigger when user clicks major value',
            payloadKeys: ['majorValue', 'trendValue'],
        },
    },
    supports: [VizBehavior.DYNAMIC_OPTIONS, VizBehavior.EVENTS, VizBehavior.PLACEHOLDER],
    themes: {}, //fixme todo implement themes
};

export default enhanceConfig(config);
